@import "../../style/sass/mixins";
@import "../../style/sass/variables";



.side-nav{
    margin-top: 1em;
    padding: 0.6em;
    box-shadow: 1px 0px 14px 0px lightgray;
    height: 97%;
    border-radius: 1em;

    .section-title{
        padding: 1em;

        h3{
            text-align: center;
        }
    }
    .individual-category{

        ul{
            padding: 0;
        }
        .sub-items{
            text-align: center;
            margin-bottom: 0.6em;
            list-style-type: none;

            a{
                text-decoration: none;
                color: black;
                @include underline-transition(0.3);
            }
        }
    }
    .price-filter-container{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }
}